"use client"
import { useState } from "react"
import Navigation from "@/components/navigation"
import SpacesPanel from "@/components/spaces-panel"
import ItemsPanel from "@/components/items-panel"
import ViewportCanvas from "@/components/viewport-canvas"
import ViewControls from "@/components/view-controls"
import ShipmentPanel from "@/components/shipment-panel"
import { Button } from "@/components/ui/button"
import { Container, containers } from "@/components/types/container"
import { PlacedBox } from "@/components/types/placedBox"

type Tab = "spaces" | "items" | "backups" | "shipments"

export default function CargoLoaderApp() {
  const [activeTab, setActiveTab] = useState<Tab>("spaces")
  const [sidebarOpen, setSidebarOpen] = useState(true)
  const [selectedContainer, setSelectedContainer] = useState<Container | null>(null)
  const [items, setItems] = useState<any[]>([])
  
  // State for placement
  const [placedBoxes, setPlacedBoxes] = useState<PlacedBox[]>([])
  const [selectedBoxId, setSelectedBoxId] = useState<string | null>(null)

  const handleSelectContainer = (id: string) => {
    const container = containers.find(c => c.id === id)
    setSelectedContainer(container || null)
  }

  // Handle placing a box in the container (called from ViewportCanvas)
  const handlePlaceBox = (itemId: string, x: number, y: number, z: number) => {
    console.log("Placing box - itemId:", itemId, "at:", x, y, z)
    
    const item = items.find(i => i.id === itemId)
    if (!item) {
      console.warn("Item not found:", itemId)
      return
    }
    
    if (item.count <= 0) {
      console.warn("Item count is 0")
      return
    }

    // Create new placed box
    const newBox: PlacedBox = {
      id: `placed-${Date.now()}-${Math.random()}`,
      itemId: item.id,
      name: item.name,
      length: item.length,
      width: item.width,
      height: item.height,
      color: item.color,
      x,
      y,
      z,
      rotation: 0
    }

    console.log("Created new box:", newBox)

    // Add to placed boxes
    setPlacedBoxes([...placedBoxes, newBox])

    // Decrement count in items
    setItems(items.map(i => 
      i.id === itemId 
        ? { ...i, count: i.count - 1 }
        : i
    ))
  }

  // Handle deleting a placed box
  const handleDeleteBox = (boxId: string) => {
    console.log("Deleting box:", boxId)
    
    const box = placedBoxes.find(b => b.id === boxId)
    if (!box) {
      console.warn("Box not found:", boxId)
      return
    }

    // Remove from placed boxes
    setPlacedBoxes(placedBoxes.filter(b => b.id !== boxId))

    // Increment count in items
    setItems(items.map(i => 
      i.id === box.itemId 
        ? { ...i, count: i.count + 1 }
        : i
    ))

    setSelectedBoxId(null)
  }

  // Handle auto-optimize
  const handleOptimize = () => {
    if (!selectedContainer || placedBoxes.length === 0) return
    
    console.log("Optimizing container layout...")
    
    const { length, width, height } = parseDimensions(selectedContainer.dimensions)
    const containerDims = {
      length: length,
      width: width,
      height: height
    }
    
    // Sort boxes: largest volume first, then heaviest first
    const sortedBoxes = [...placedBoxes].sort((a, b) => {
      const volumeA = (a.length * a.width * a.height)
      const volumeB = (b.length * b.width * b.height)
      return volumeB - volumeA
    })
    
    const optimizedBoxes: PlacedBox[] = []
    
    // Simple bottom-left-back packing algorithm
    for (const box of sortedBoxes) {
      const boxLength = box.length / 100
      const boxWidth = box.width / 100
      const boxHeight = box.height / 100
      
      let placed = false
      
      // Try to place on the floor first, scanning from back-left corner
      const step = 0.1 // 10cm steps for faster computation
      
      for (let z = -containerDims.width / 2 + boxWidth / 2; z <= containerDims.width / 2 - boxWidth / 2 && !placed; z += step) {
        for (let x = -containerDims.length / 2 + boxLength / 2; x <= containerDims.length / 2 - boxLength / 2 && !placed; x += step) {
          // Calculate Y with gravity (check what's below)
          let y = boxHeight / 2
          
          for (const placedBox of optimizedBoxes) {
            const pBoxLength = placedBox.length / 100
            const pBoxWidth = placedBox.width / 100
            const pBoxHeight = placedBox.height / 100
            
            const xOverlap = Math.abs(x - placedBox.x) < (boxLength / 2 + pBoxLength / 2)
            const zOverlap = Math.abs(z - placedBox.z) < (boxWidth / 2 + pBoxWidth / 2)
            
            if (xOverlap && zOverlap) {
              const topOfBox = placedBox.y + pBoxHeight / 2
              const proposedY = topOfBox + boxHeight / 2
              if (proposedY > y) {
                y = proposedY
              }
            }
          }
          
          // Check if this position is valid
          if (y + boxHeight / 2 > containerDims.height) continue
          
          // Check collision with all placed boxes
          const hasCollision = optimizedBoxes.some(placedBox => {
            const pBoxLength = placedBox.length / 100
            const pBoxWidth = placedBox.width / 100
            const pBoxHeight = placedBox.height / 100
            
            const xOverlap = Math.abs(x - placedBox.x) < (boxLength / 2 + pBoxLength / 2)
            const yOverlap = Math.abs(y - placedBox.y) < (boxHeight / 2 + pBoxHeight / 2)
            const zOverlap = Math.abs(z - placedBox.z) < (boxWidth / 2 + pBoxWidth / 2)
            
            return xOverlap && yOverlap && zOverlap
          })
          
          if (!hasCollision) {
            // Found valid position!
            optimizedBoxes.push({
              ...box,
              x,
              y,
              z
            })
            placed = true
          }
        }
      }
      
      // If couldn't place on floor, try stacking on existing boxes
      if (!placed) {
        console.warn("Could not place box:", box.name)
        // Keep original position as fallback
        optimizedBoxes.push(box)
      }
    }
    
    console.log("Optimization complete. Placed:", optimizedBoxes.length, "boxes")
    setPlacedBoxes(optimizedBoxes)
  }

  // Helper function to parse dimensions
  const parseDimensions = (dimensions: string) => {
    const cleaned = dimensions.replace(' cm', '').trim()
    const parts = cleaned.split('x').map(s => parseFloat(s.trim()))
    
    if (parts.length === 3) {
      return {
        length: parts[0] / 100,
        width: parts[1] / 100,
        height: parts[2] / 100
      }
    }
    
    return { length: 12, width: 2.4, height: 2.4 }
  }

  return (
    <div className="flex h-screen flex-col bg-gray-50">
      <Navigation activeTab={activeTab} onTabChange={setActiveTab} />
      
      <div className="flex flex-1 overflow-hidden">
        {/* Left Sidebar */}
        {sidebarOpen && (
          <aside className="w-80 border-r border-gray-200 bg-white">
            {activeTab === "spaces" && (
              <SpacesPanel
                onToggle={() => setSidebarOpen(!sidebarOpen)}
                selectedContainer={selectedContainer?.id || null}
                onSelectContainer={handleSelectContainer}
              />
            )}
            {activeTab === "items" && (
              <ItemsPanel 
                onToggle={() => setSidebarOpen(!sidebarOpen)} 
                items={items} 
                onItemsChange={setItems}
              />
            )}
          </aside>
        )}

        {/* Main Content Area */}
        <main className="relative flex flex-1 flex-col">
          {/* Controls when box is selected */}
          {selectedBoxId && (
            <div className="absolute top-4 left-1/2 -translate-x-1/2 z-10 bg-gray-800 text-white px-6 py-3 rounded-lg shadow-lg flex items-center gap-3">
              <span className="font-semibold">Box Selected</span>
              <button
                onClick={() => handleDeleteBox(selectedBoxId)}
                className="px-4 py-1 bg-red-500 hover:bg-red-600 rounded text-sm transition-colors"
              >
                Delete
              </button>
              <button
                onClick={() => setSelectedBoxId(null)}
                className="px-4 py-1 bg-gray-600 hover:bg-gray-700 rounded text-sm transition-colors"
              >
                Cancel
              </button>
            </div>
          )}

          {/* 3D Viewport */}
          <div className="flex-1 bg-gray-100">
            <ViewportCanvas 
              container={selectedContainer} 
              items={items}
              placedBoxes={placedBoxes}
              selectedBoxId={selectedBoxId}
              onPlaceBox={handlePlaceBox}
              onSelectBox={setSelectedBoxId}
            />
          </div>

          {/* Bottom Shipment Panel */}
          <ShipmentPanel container={selectedContainer} />

          {/* Optimize Button */}
          <Button
            size="lg"
            onClick={handleOptimize}
            disabled={!selectedContainer || placedBoxes.length === 0}
            className="absolute bottom-32 right-8 h-24 w-24 rounded-full bg-blue-400 text-2xl font-semibold text-white shadow-lg hover:bg-blue-500 disabled:bg-gray-300 disabled:cursor-not-allowed"
          >
            Optimize
          </Button>
        </main>
      </div>
    </div>
  )
}